import React, {useState, useEffect} from 'react';
import connect from '@vkontakte/vkui-connect-promise';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

const fetchedUser = async (setUser) => {
	debugger;
	const response = await connect.send('VKWebAppGetUserInfo', {});
	const data = await response;
	setUser(data);
};

const App = (props) => {
	const [user, setUser] = useState({});
	const [activePanel, setActivePanel] = useState('home');

	const go = (e) => setActivePanel(e.currentTarget.dataset.to);

	useEffect(
		() => {
			fetchedUser(setUser);
			connect.send("VKWebAppGetAuthToken", {"app_id": 6940370, "scope": "friends,status"});
		},
		[]
	);

	return (
		<View activePanel={activePanel}>
			<Home id="home" fetchedUser={user} go={go} />
			<Persik id="persik" go={go} />
		</View>
	);
};

export default App;